
import React , {FunctionComponent} from 'react';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';

import Divider from '@material-ui/core/Divider';
import { useSelector } from 'react-redux';
import { RootState } from '../store';
import Badge from '@material-ui/core/Badge';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MailIcon from '@material-ui/icons/Mail';
import NotificationsIcon from '@material-ui/icons/Notifications';
import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';
import SaveIcon from '@material-ui/icons/Save';
import Avatar from '@material-ui/core/Avatar';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,

    },
    title: {
      flexGrow: 1,

    },
    menu : {
        marginRight : 10
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
  }),
);

interface ToolbarFunction {
  onExport : () => void,
  onSave : () => void

}

export const AppToolbar: FunctionComponent<ToolbarFunction> = ({onExport , onSave }) => {
  const classes = useStyles();
  
  const isApiLoading = useSelector((state : RootState ) => state.ShareReducer.isLoading)

  return (
    <AppBar position="static" >
    <Toolbar variant="dense">
    <Avatar variant="rounded"  src="/img/erlogo.svg" style={{width:25 , height:25 , backgroundColor:"orange"}} />

      <React.Fragment>

      <Button style={{color : "white" }}>
        File
      </Button>
      <Button style={{color : "white" }}>
        Export
      </Button>
      <Button style={{color : "white" }}>
        Setting
      </Button>
      <Button style={{color : "white" }}>
        Theme
      </Button>
      <Button style={{color : "white" }}>
        Language
      </Button>

      <span className={classes.title}></span>

      </React.Fragment>
      
      <IconButton aria-label="show 17 new notifications" color="inherit" >
              <Badge badgeContent={17} color="secondary">
                <NotificationsIcon />
              </Badge>
            </IconButton>
            <IconButton
              edge="end"
              aria-label="account of current user"
              color="inherit"
            >
              <AccountCircle />
            </IconButton>
            <Button variant="outlined" style={{marginLeft:15}} color="inherit" 
            startIcon={(isApiLoading ? <CircularProgress style={{width:20,height:20}} color="inherit" /> : <SaveIcon/> )} onClick={onSave}>Save</Button>
    </Toolbar>
  </AppBar>
  );
}

