import React, { FunctionComponent, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Route, Switch } from "react-router-dom";

import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';

import IconButton from '@material-ui/core/IconButton';

import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';

import {Sidebar} from '../components/Document/Sidebar'
import LoginModal from "../components/Document/LoginModal";
import { MainHead } from "../components/Document/MainHead";
import { RootState } from "../store";
import { initApp } from "../reducer/loadapp";
import {

  Loader,


} from "semantic-ui-react";
import { FileList } from "../components/Document/FileList";
import { ToggleModal } from "../reducer/project/action";
import { ProjectForm } from "../components/Document/ProjectForm";

export default function ExTemplate() {
  return <div>Template</div>;
}

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  menuButtonHidden: {
    display: 'none',
  },
  title: {
    flexGrow: 1,
  },
  drawerPaper: {
    position: 'relative',
    whiteSpace: 'nowrap',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9),
    },
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto',
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
  fixedHeight: {
    height: 240,
  },
}));

export const Document: FunctionComponent = () => {
  const isLogin = useSelector((state: RootState) => state.AuthReducer.isLogin);

  const classes = useStyles();

  const addProjectModal = useSelector((state: RootState) => state.ProjectReducer.addProjectModal)

  const isLoading = useSelector(
    (state: RootState) => state.ShareReducer.isLoading
  );

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(initApp());
  }, []);

  return (

    <div className={classes.root}>
      <Backdrop open={isLoading} >
        <CircularProgress color="inherit" />
      </Backdrop>
      <ProjectForm isOpen={addProjectModal} />
    <CssBaseline />
    <MainHead></MainHead>
    <Sidebar/>
    <main className={classes.content}>
      <div className={classes.appBarSpacer} />
      <Container maxWidth="lg" className={classes.container}>
        <Grid container spacing={3}>
          {/* Chart */}
          <Switch>
              <Route path="/document/filelist" component={FileList} />
              <Route path="/document/template" component={ExTemplate} />
            </Switch>

        </Grid>
        
      </Container>
    </main>
  </div>
    

  );
};
